<template>
  <div class="fuente">
      <b-container class='aireTopGeneral'>
        <b-row>
                <b-col cols='12' class='text-center'>
                     <h2>Perfil frontEnd</h2>
                </b-col>
        </b-row>
      </b-container>
       <b-container class='aireTopGeneral'>
        <b-row>
                <b-col cols="12" sm="4" md="3" lg="2" xl="2" class='text-center'>
                     <b-img class="logoHtml5" src="../assets/HTML5.svg" fluid alt="" />
                </b-col>
                <b-col cols="12" sm="4" md="3" lg="2" xl="2" class='text-center'>
                     <b-img src="../assets/CSS3.svg" fluid alt="" />
                </b-col>
                <b-col cols="12" sm="4" md="3" lg="2" xl="2" class='text-center'>
                     <b-img src="../assets/Js.svg" fluid alt="" />
                </b-col>
                 <b-col cols="12" sm="4" md="3" lg="2" xl="2" class='text-center'>
                     <b-img src="../assets/Sass.svg" fluid alt="" />
                </b-col>
                <b-col cols="12" sm="4" md="3" lg="2" xl="2" class='text-center'>
                     <b-img src="../assets/Bootstrap.svg" fluid alt="" />
                </b-col>
                <b-col cols="12" sm="4" md="3" lg="2" xl="2" class='text-center'>
                     <b-img src="../assets/Nodejs.svg" fluid alt="" />
                </b-col>
        </b-row>
         <b-row>
                <b-col cols="12" sm="6" md="6" lg="6" xl="6" class='alinearAngular'>
                     <b-img class="logoAngular" src="../assets/Angularjs.svg" fluid alt="" />
                </b-col>
                <b-col cols="12" sm="6" md="6" lg="6" xl="6" class='alinearVue'>
                     <b-img  class="reducirLogoVue logoVue" src="../assets/Vue.png" fluid alt="" />
                </b-col>
        </b-row>
      </b-container>
      <div class='aireTopGeneral' id='container'>

      </div>
  </div>
</template>

<script>
export default {
  name: 'quesehacer',
  data () {
    return {
      camera: null,
      scene: null,
      renderer: null,
      mesh: null,
      material: null,
      stats: null
    }
  },
  methods: {
    init: function () {

    }
  },
  mounted () {
    this.init()
  }
}
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style rel='stylesheet/scss' lang='scss' scoped>
@import 'src/styles/index.scss';

.fuente{
  font-family: 'Lato light';
}

.reducirLogoVue{
   width:30%;
}

.alinearAngular{
  text-align: right;
}

.alinearVue{
  text-align: left;
}

h2{
    color: white;
    text-shadow: 1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue;
    perspective: 1em;
    transform-style: preserve-3d;
}

.logoHtml5{
  animation: logoHtml5 6s infinite ease-in;
  @keyframes logoHtml5 {
    0% { transform: rotate3d(0, 1, 0, 0deg); margin-left:2em;}
    25% { transform: rotate3d(0, 1, 0, 0deg); margin-right:0;}
    50% { transform: rotate3d(0, 1, 0, 180deg); margin-left:2em;}
    100% { transform: rotate3d(0, 1, 0, 360deg); margin-left:2em;}
  }

}

.logoAngular{
  animation: logoAngularX 5s infinite ease-in;
  @keyframes logoAngularX {
    0% { transform: rotate3d(1, 1, 1, 0deg);margin-left:4em;}
    25% { transform: rotate3d(1, 1, 1, 0deg);margin-left:0}
    50% { transform: rotate3d(1, 1, 1, 180deg);margin-left:2em;}
    100% { transform: rotate3d(1, 1, 1, 360deg);margin-left:0;}
  }
}

.logoVue{
  animation: logoVueX 6s infinite ease-in-out;
  @keyframes logoVueX {
    0% { transform: rotate3d(0, 1, 1, 0deg);margin-right:4em;}
    25% { transform: rotate3d(0, 1, 1, 90deg);margin-right:0}
    50% { transform: rotate3d(0, 1, 1, 180deg);margin-right:2em;}
    100% { transform: rotate3d(0, 1, 1, 360deg);margin-right:0;}
  }
}

h2:hover {
  /*-ms-transform: scale(1.5); //IE 9
  -webkit-transform: scale(1.5); // Safari 3-8
   transform: scale(1.5);*/
/*
    animation-name: example;
    animation-duration: 4s;
    animation-iteration-count: infinite;
    animation-iteration-count: 2;//si quieres especificar las veces que se ejecute
    animation-direction: alternate-reverse;// para que se ejecute al reves y pa lante.
    {animation-timing-function: linear;} // indica la velocidad de la animacion
    {animation-timing-function: ease;}
    {animation-timing-function: ease-in;}
    {animation-timing-function: ease-in-out;}

  */
  animation: miAnimacion 2s infinite linear;
  @keyframes miAnimacion {
    0% { transform: rotate3d(0, 1, 0, 0deg); }
    50% { transform: rotate3d(0, 1, 0, 180deg); }
    100% { transform: rotate3d(0, 1, 0, 360deg); }
  }

}

@media only screen and (max-width: 576px){
  .alinearAngular{
    text-align: center;
  }

  .alinearVue{
    text-align: center;
  }

 }

</style>



